import React from "react"
import "../style.css"
import Appbar from "../components/appbar"
import { graphql } from "gatsby"
import ExpCard from "../components/experience_card"
import { Helmet } from "react-helmet"

function WorkExperience({ data }) {
  const projectList = data.allWorkYaml.edges

  return (
    <div className="container-fluid">
      <Appbar />
      <Helmet>
        <meta charSet="utf-8" />
        <title>Work Experience | Kaustubh</title>
      </Helmet>
      <div className="row">
        {projectList.map(({ node }) => {
          return (
            <ExpCard
              cardTitle={node.company}
              cardSubtitle={node.position}
              cardDuration={node.duration}
              key={node.id}
              cardImage={node.image}
              cardLocation={node.location}
              readMore={node.readMore}
            />
          )
        })}
      </div>
    </div>
  )
}

export default WorkExperience


export const query = graphql`
  {
    allWorkYaml {
      edges {
        node {
          company
          position
          duration
          id
          image
          location
          readMore
        }
      }
    }
  }
`
